
import {defineComponent, ref} from 'vue';
import {IonInput, IonItem, IonPage, IonButton, IonContent, IonSpinner, IonModal, IonText} from '@ionic/vue';
import router from '@/router'; // Vue Router 설정 가져오기

export default defineComponent({
  name: "LoginView",
  components: {
    IonInput,
    IonItem,
    IonPage,
    IonButton,
    IonContent,
    IonSpinner,
    IonModal,
    IonText,
  },
  setup() {
    const username = ref('');
    const password = ref('');
    const isLoading = ref(false);
    const showModal = ref(false);
    const modalMessage = ref('');

    const login = async () => {
      isLoading.value = true;
      setTimeout(() => {
        if (username.value === 'admin' && password.value === 'okestro2018') {
          modalMessage.value = '로그인 성공';
          showModal.value = true;
          localStorage.setItem('loggedIn', 'true');
          localStorage.setItem('username', username.value);
          router.push('/tabs/tab1');
        } else {
          modalMessage.value = '로그인 실패';
          showModal.value = true;
        }
        isLoading.value = false;
      }, 2000);
    };

    const closeModal = () => {
      showModal.value = false;
    };

    return {
      username,
      password,
      login,
      isLoading,
      showModal,
      modalMessage,
      closeModal,
    };
  },
});
